.item{
    background: black;
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index:-1;
}
.customHeading{
    color:var(--secoundary);
    font-family: var(--font);
    font-size: 90px;
    padding-bottom: 2%;
}
.customHeadingSection{
  flex-direction: column;
  align-items: center;
}
.customHeadingButton{
    margin-top: 7%;
    padding:2%;
    margin: 1%;
    border-radius:10px;
    background-color: var(--secoundary);
    color:var(--primary);
    transition: all .5s;
    outline: none;
    white-space: nowrap;
    border: none;
}
.customHeadingButton:hover{
    background-color: var(--primary);
    color:var(--secoundary)
}

.customHeadingEditButton{
    margin-top: 7%;
    margin: auto;
    padding:5%;
    border-radius:10px;
    background-color: var(--primary);
    color:var(--secoundary);
    transition: all .5s;
    outline: none;
    border: none;
    white-space: nowrap;
    margin-left: 20px;
}
.customHeadingEditButton:hover{
    color: var(--primary);
    background-color:var(--secoundary)
}