.form-control {
    background-color: var(--secoundary) !important;
    outline: none;
}

.customLabel {
    padding-left: 40px !important;
}

.form-control:focus {
    border: none !important;
    outline: none !important;
}

.text-color-s {
    color: var(--secoundary);
}

.teamMemberSection {
    margin: 1% 0% 1% 0%;
    border-top: 1px solid var(--secoundary);
}

.table {
    color: var(--secoundary) !important;
}

.customInput {
    padding: 10px;
    border: none;
    outline: none;
    width: 100%;
    background-color: var(--secoundary);
}

.hide {
    display: none;
}

.error {
    color: red;
}

.dropdown-item {
    background-color: var(--secoundary);
}

.dropdown-menu {
    background-color: var(--secoundary);
}

.dropdown-item:hover {
    background-color: var(--primary);
    color: var(--secoundary);
}

/* width */
::-webkit-scrollbar {
    width: 20px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 10px;
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: var(--secoundary); 
    border-radius: 10px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: var(--secoundary); 
  }

