     .rs-menu h1 {
         font-size: 3.8rem;
         line-height: 1.2em;
     }
 
     .rs-menu h2 {
         font-size: 2rem;
         line-height: 1.2em;
     }
 
     .rs-menu h3 {
         font-size: 3.3rem;
     }
 
     .rs-menu h4 {
         font-size: 2.8rem;
     }
 }
 
