       width: 91.45833333333334%;
    }
 
    .rs-menu .row .col-10 {
       width: 82.91666666666667%;
    }
 
    .rs-menu .row .col-9 {
       width: 74.375%;
    }
 
    .rs-menu .row .col-8 {
       width: 65.83333333333334%;
    }
 
    .rs-menu .row .col-7 {
       width: 57.29166666666667%;
    }
 
    .rs-menu .row .col-6 {
      width: 48.75%;
    }
 
    .rs-menu .row .col-5 {
      width: 40.208333333333336%;
    }
 
    .rs-menu .row .col-4 {
       width: 31.666666666666668%;
    }
 
    .rs-menu .row .col-3 {
       width: 23.125%;
    }
 
    .rs-menu .row .col-2 {
       width: 14.583333333333334%;
    }
 
    .rs-menu .row .col-1 {
       width: 6.041666666666667%;
    }
     /*-----------------------------------------------------------------------------------*/
     /*  RESPONSE MENU
     /*-----------------------------------------------------------------------------------*/
     .rs-menu li {
         display:inline-block;
     }
     
     .nav-menu .rs-mega-menu {
         position: static;
     }
     
     .rs-menu ul ul.visible {
         visibility: hidden;
         opacity:0;
     }
     
     /* Remove arrows if not in mobile */
     .nav-menu .menu-item-has-children > span, .nav-menu .rs-mega-menu > span {
         display:none;
     }
     
     /* Remove Menu Toggle if not in mobile */	
     .rs-menu-toggle {
         display:none;
     }
     
     /* Remove Menu Close if not in mobile */
     .nav-menu div.sub-menu-close {
         display:none;
     }
     
     /* Insert arrows down */
     .nav-menu .rs-mega-menu > a:after, .nav-menu > .menu-item-has-children > a:after {
         content:"\f107";
         font-family:'Font Awesome\ 5 Free';
         font-weight: 900;
         float:right;
         margin:2px 0 0 5px;
     }
     
     /* Put arrows to the right */
     .nav-menu ul .menu-item-has-children > a:after {
         content:"\f105";
         font-family:'Font Awesome\ 5 Free';
         font-weight: 900;
         float:right;
     }
 
     /* Show sub-menus in hover */
     .rs-menu li:active > ul, .rs-menu li:focus > ul, 
     .rs-menu li:hover > ul, .rs-menu li:hover > ul.mega-menu > li > ul, 
     .rs-menu li:hover ul.mega-menu .sub-menu {		
         display:block;
         z-index: 9999;	
     }
 
     /* Adjust position the sub-menus in the left */
     .rs-menu ul ul li > ul {
         left:100%;
     }
     
     /* Adjust position the sub-menus on the top */
     .rs-menu ul ul ul {
         top:-4px;
     }	
     
     .rs-menu ul ul {
         width:auto;
     }
     
     /* Sub-menus - width */
     .rs-menu ul ul a {
         width:240px;	/* <- Modify for width change */
     }
     
     .nav-menu .mega-menu li div.mega-menu-img a {	
         width:25%;
     }
     
     
     .nav-menu .mega-menu .mega-menu-container {		
         padding: 16px;
     }
         
 }
 
 /*-----------------------------------------------------------------------------------*/
 /*  ARROWS FIXES IN IE
 /*-----------------------------------------------------------------------------------*/
 @media screen and (min-width:0\0) {
     .rs-menu img {
         width:100%;    	
     }
     
     .nav-menu .menu-item-has-children a, .nav-menu .rs-mega-menu a {
         padding-right:24px
     }
     
     .nav-menu .menu-item-has-children a:after, .nav-menu .rs-mega-menu a:after {
         position:static;
         float:right;
         margin:0 -16px 0 0
     }   
 }
 
 @media only screen and (max-width: 991px) {
    .rs-menu .row [class*="col-"] {
        float: left!important;
        width: 100%!important;
        margin-left: 0!important;
        margin-right: 0!important;
     }
    
     .nav-menu > li > a {
         border-bottom:1px solid #131313;
         color: #fff !important;
         text-align: left;
     }
     
     .nav-menu > li:last-child > a {
         border-bottom:none;
     }
     
     .nav-menu span.rs-menu-parent i { 
         margin:0;
     }
     
     .nav-menu > .menu-item-has-children > span.rs-menu-parent, .nav-menu .rs-mega-menu > span.rs-menu-parent {		
         display: block;
         position: absolute;
         right: 0;
         top: 0;
         padding: 20px;	
         cursor:pointer;
         line-height: 15px;
         background: #181818;
         border-left: 1px solid #131313;		
         
         /* Remove the blue Webkit background when element is tapped */
         -webkit-tap-highlight-color:rgba(0,0,0,0);    
     }	
     
     .nav-menu > .menu-item-has-children > span.rs-menu-parent:hover, .nav-menu .rs-mega-menu > span.rs-menu-parent:hover {	
         color: #fff;
     }
     
     .nav-menu > .menu-item-has-children .sub-menu .menu-item-has-children span.rs-menu-parent {		
         display: block;
         position: absolute;
         right: 0;
         top: 0;
         padding: 20px;	
         cursor:pointer;
         line-height: 15px;
         border-left:1px solid rgba(0, 0, 0, .05);		
         
         /* Remove the blue Webkit background when element is tapped */
         -webkit-tap-highlight-color:rgba(0,0,0,0);    
     }
 
     /*Options Menu Closed*/
     .nav-menu div.sub-menu-close {
         float:left;
         width:100%;
         padding:16px;
         cursor:pointer;
         background:#f6f3ed;
         
         color:#000;
         font-size:14px;
         text-align:center;		
     }
     
     .nav-menu div.sub-menu-close i {		
         font-size:16px;
         margin-right: 5px;
     }
     
     .rs-menu .sub-menu {
         -webkit-box-shadow: 0 0 20px rgba(0,0,0,.15);
         -moz-box-shadow: 0 0 20px rgba(0,0,0,.15);
         -ms-box-shadow:  0 0 20px rgba(0,0,0,.15);
         -o-box-shadow:  0 0 20px rgba(0,0,0,.15);
         box-shadow:  0 0 20px rgba(0,0,0,.15);
     }
     
     .rs-menu-close {
         height: 0;		
         overflow: hidden!important;
     }
     
     /*Menu Toggle Appearance*/
     .rs-menu-toggle {      
         padding: 16px;
         display: block;
         background: #222;
         border-left: 1px solid #222;
         position: relative;
         cursor:pointer;		
         color: #eee;
         font-size: 15px;
         text-transform: uppercase;
         
         /* Remove the blue Webkit background when element is tapped */
         -webkit-tap-highlight-color:rgba(0,0,0,0);
     }
     
     .rs-menu-toggle i {
         margin-right:10px; 
     }
     
 }
 
 @media only screen and (max-width: 1080px) {	
     .nav-menu > li > a i {
         display:none;
     }
 }
 
 
